<template>
  <div class="custom-wrap">
    <el-row type="flex" class="row-bg" justify="space-between">
      <el-col :span="24">
        <el-row type="flex" class="row-bg" justify="space-between">
          <el-col :span="12">
            <h3>Web白名单规则配置</h3>
          </el-col>
          <el-col :span="12" class="text-align-right">
            <a v-if="ruleType == 'single_rule'" class="el-button el-button--primary is-plain"
              :href="'/#/protection/' + domain + '/' + ruleType">返回</a>
            <a v-if="ruleType == 'group_rule'" class="el-button el-button--primary is-plain"
              :href="'/#/group-protection/' + domain">返回</a>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
    <div class="margin-4x"></div>
    <el-row v-loading.fullscreen.lock="loadingPage">
      <el-col :span="24">
        <el-row class="text-align-right">
          <el-button type="success" @click="onClickChangeOrder()">优先级调整</el-button>
          <el-button type="primary" @click="onClickDownloadRule()" :loading="loadingDownloadRule">加载规则</el-button>

        </el-row>
        <div class="demo-block">
          <el-table :data="tableData" style="width: 100%">
            <el-table-column prop="rule_name" label="名称"></el-table-column>
            <el-table-column prop="rule_detail" label="描述"></el-table-column>
            <el-table-column prop="" label="类型">
              <template #default="scope">
                <p v-if="scope.row.rule_type == 'group_rule'">
                  规则组
                </p>
                <p v-if="scope.row.rule_type == 'single_rule'">
                  规则
                </p>
              </template>
            </el-table-column>
            <el-table-column prop="rule_status" label="状态">
              <template #default="scope">
                <el-switch v-model="scope.row.rule_status" @change="onChangeRuleStatus(scope.row)" active-value="true"
                  inactive-value="false" />
              </template>
            </el-table-column>

            <el-table-column label="操作" align="right">
              <template #default="scope">
                <el-button size="mini" @click="handleLook(scope.row)" class="button-block" type="text">查看</el-button>
                <el-popover placement="top" width="160" v-model:visible="scope.row.isVisiblePopover">
                  <p>确定删除吗？</p>
                  <div style="text-align: right; margin: 0">
                    <el-button size="mini" type="text" @click="scope.row.isVisiblePopover = false">取消</el-button>
                    <el-button type="primary" size="mini" @click="handleDelete(scope.row)" :loading="loading">确定
                    </el-button>
                  </div>
                  <template #reference>
                    <el-button type="text" size="mini" @click="scope.row.isVisiblePopover = true">删除</el-button>
                  </template>
                </el-popover>
              </template>
            </el-table-column>
            <el-table-column label="优先级" align="right" v-if="!isShowOrder">
              <template #default="scope">
                <el-button type="success" class="icon iconfont iconxiangshang" circle
                  @click="onClickChangeOrderSubmit(scope.$index, scope.row, 'up')" title="上移" :loading="orderLoading" />
                <el-button type="success" class="icon iconfont iconxiangxia" circle
                  @click="onClickChangeOrderSubmit(scope.$index, scope.row, 'down')" title="下移"
                  :loading="orderLoading" />
                <el-button type="success" class="icon iconfont iconzhiding" circle
                  @click="onClickChangeOrderSubmit(scope.$index, scope.row, 'top')" title="置顶"
                  :loading="orderLoading" />
              </template>
            </el-table-column>

          </el-table>
        </div>
      </el-col>

      <el-dialog v-model="dialogDownloadRuleFormVisible" title="加载" :close-on-click-modal="false" width="520px"
        @closed="dialogCloseDownloadRule">

        <el-form class="form-download-rule-dialog" :model="downloadRuleForm" label-position="left" label-width="30px"
          :rules="rules" ref="downloadRuleForm">

          <el-tabs tab-position="left" style="height: 150px" @tab-click="handleTabClick" v-model="tabIndex">
            <el-tab-pane label="加载规则" name="single_rule">
              <el-form-item label="" key="1">
                <el-select v-model="downloadRuleForm.rule_uuid" placeholder="请选择或输入模糊搜索" filterable>
                  <el-option v-for="item in ruleOptions" :key="item.rule_uuid" :label="item.rule_name"
                    :value="item.rule_uuid">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-tab-pane>
            <el-tab-pane label="加载规则组" name="group_rule">
              <el-form-item label="" key="2">
                <el-select v-model="downloadRuleForm.rule_uuid" placeholder="请选择或输入模糊搜索" filterable>
                  <el-option v-for="item in ruleGroupOptions" :key="item.rule_uuid" :label="item.rule_group_name"
                    :value="item.rule_uuid">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-tab-pane>
          </el-tabs>

        </el-form>
        <template #footer class="dialog-footer">
          <el-button @click="dialogDownloadRuleFormVisible = false">取消</el-button>
          <el-button type="primary" @click="onClickDownloadRuleSubmit('downloadRuleForm')" :loading="loading">确定
          </el-button>
        </template>
      </el-dialog>

      <el-dialog v-model="dialogLookRuleFormVisible" title="查看规则" :close-on-click-modal="false" width="520px">
        <el-form class="form-look-rule-dialog" :model="lookRuleForm" label-position="left" label-width="130px">
          <el-form-item label="规则名称" key="1">
            {{lookRuleForm.rule_name}}
          </el-form-item>
          <el-form-item label="规则详情" key="2">
            {{lookRuleForm.rule_detail}}
          </el-form-item>
          <el-card class="box-card-rule" shadow="never">
            <div class="card-item" v-for="(bigItem, bigIndex) in ruleBigMatchs" :key="bigIndex">
              <el-form-item label="匹配参数">
                <div class="match-box" v-for="(item, index) in bigItem.ruleSmallMatchs" :key="index">
                  <div class="match-box-content">
                    <div class="match_key_cascader">
                      {{ item.rule_match_key }}
                    </div>
                  </div>
                </div>
              </el-form-item>

              <el-form-item label="参数处理">
                <div class="match-box" v-for="(item, index) in bigItem.argsPrepocessList" :key="index">
                  <div class="match-box-content">
                    <div class="match_key_cascader">
                      {{ item.args_prepocess_value }}
                    </div>
                  </div>
                </div>
              </el-form-item>
              <el-form-item label="匹配方式">
                {{ bigItem.match_operator }}
              </el-form-item>
              <el-form-item label="匹配内容">
                {{ bigItem.match_value }}
              </el-form-item>
            </div>
          </el-card>
          <el-form-item label="执行动作">
            {{ lookRuleForm.rule_action }}
          </el-form-item>
          <el-form-item label="执行内容">
            {{ lookRuleForm.action_value}}
          </el-form-item>
          <el-form-item label="日志记录">
            {{ lookRuleForm.rule_log }}
          </el-form-item>
        </el-form>
        <template #footer class="dialog-footer">
          <el-button @click="dialogLookRuleFormVisible = false">关闭</el-button>
        </template>
      </el-dialog>


      <el-dialog v-model="dialogLookRuleGroupFormVisible" title="查看规则组" :close-on-click-modal="false" width="800px">

        <el-table :data="lookRuleGroupData" style="width: 100%" :row-key="getRowKey" :expand-row-keys="rowExpend">
          <el-table-column type="expand">
            <template #default="props">
              <el-form class="form-look-rule-group-dialog" label-position="left" label-width="130px">
                <el-form-item label="规则名称" key="1">
                  {{ props.row.rule_name}}
                </el-form-item>
                <el-form-item label="规则详情" key="2">
                  {{ props.row.rule_detail}}
                </el-form-item>
                <el-card class="box-card-rule" shadow="never">
                  <div class="card-item" v-for="(bigItem, bigIndex) in props.row.ruleBigMatchs" :key="bigIndex">
                    <el-form-item label="匹配参数">
                      <div class="match-box" v-for="(item, index) in bigItem.ruleSmallMatchs" :key="index">
                        <div class="match-box-content">
                          <div class="match_key_cascader">
                            {{ item.rule_match_key }}
                          </div>
                        </div>
                      </div>
                    </el-form-item>

                    <el-form-item label="参数处理">
                      <div class="match-box" v-for="(item, index) in bigItem.argsPrepocessList" :key="index">
                        <div class="match-box-content">
                          <div class="match_key_cascader">
                            {{ item.args_prepocess_value }}
                          </div>
                        </div>
                      </div>
                    </el-form-item>
                    <el-form-item label="匹配方式">
                      {{ bigItem.match_operator }}
                    </el-form-item>
                    <el-form-item label="匹配内容">
                      {{ bigItem.match_value }}
                    </el-form-item>
                  </div>
                </el-card>
                <el-form-item label="执行动作">
                  {{ props.row.rule_action }}
                </el-form-item>
                <el-form-item label="执行内容">
                  {{ props.row.action_value}}
                </el-form-item>
                <el-form-item label="日志记录">
                  {{ props.row.rule_log }}
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column prop="rule_name" label="规则组名称"></el-table-column>
          <el-table-column prop="rule_uuid" label="UUID"></el-table-column>
          <el-table-column prop="rule_detail" label="描述"></el-table-column>

          <el-table-column label="操作" align="right">
            <template #default="scopeGroup">
              <el-button size="mini" @click="handleLookGroup(scopeGroup.row)" class="button-block" type="text">
                查看
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <template #footer class="dialog-footer">
          <el-button @click="dialogLookRuleGroupFormVisible = false">关闭</el-button>
        </template>
      </el-dialog>
    </el-row>

  </div>
</template>
<script>
import { mixin, JXAjax } from "../assets/scripts/common";
import { useRoute } from 'vue-router'
export default {
  mixins: [mixin],
  data() {
    return {
      loading: false,
      loadingPage: true,
      isShowOrder: true,
      orderLoading: false,
      tableData: [],
      domain: "",
      ruleType: "",
      dialogDownloadRuleFormVisible: false,
      loadingDownloadRule: false,
      downloadRuleForm: [],
      ruleOptions: [],
      ruleGroupOptions: [],
      dialogLookRuleFormVisible: false,
      lookRuleForm: [],
      tabIndex: "single_rule",
      dialogLookRuleGroupFormVisible: false,
      lookRuleGroupData: [],
      lookRuleGroupForm: {},
      ruleBigMatchs: [],
      getRowKey(row) { 
        return row.rule_uuid;
      },
      rowExpend:[],
    };
  },
  computed: {
    rules() {
      return {
        rule_uuid: [
          {
            required: true,
            message: '请选择一个规则',
            trigger: 'change',
          },
        ],
      }
    }
  },
  mounted() {
    const route = useRoute()
    this.ruleType = route.params.ruleType;
    this.domain = route.params.domain;
    this.getData();
  },
  methods: {
    getData() {
      var t = this;
      var postUrl = "/waf/waf_get_web_white_rule_list";
      var oData = { domain: t.domain };
      if (t.ruleType == "group_rule") {
        postUrl = "/waf/waf_get_group_web_white_rule_list";
        oData = { group_id: t.domain }
      }
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.loadingPage = false;
          t.tableData = response.data.message;
          t.tableData.forEach(item => {
            item.isVisiblePopover = false;
          })
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    handleTabClick(tab) {
      var t = this;
      t.downloadRuleForm.rule_uuid = "";
      if (t.ruleGroupOptions.length == 0 && t.tabIndex == "group_rule") {
        t.getGroupRuleList();
      }
      if (t.ruleOptions.length == 0 && t.tabIndex == "single_rule") {
        t.getRuleList();
      }
    },
    onClickDownloadRule() {
      var t = this;
      t.dialogDownloadRuleFormVisible = true;
      if (t.tabIndex == "single_rule") {
        t.getRuleList();
      } else {
        t.getGroupRuleList();
      }
    },
    getRuleList() {
      var t = this;
      t.loadingDownloadRule = true;
      JXAjax(
        "post",
        "/waf/waf_get_sys_web_white_rule_list",
        { rule_type: "single_rule" },
        function (response) {
          t.loadingDownloadRule = false;
          var _ruleOptions = response.data.message;
          t.ruleOptions = t.getDifference(t.tableData, _ruleOptions)
        },
        function () {
          t.loadingDownloadRule = false;
        },
        "no-message"
      );
    },
    getGroupRuleList() {
      var t = this;
      JXAjax(
        "post",
        "/waf/waf_get_sys_web_white_rule_group_list",
        {},
        function (response) {
          t.loadingDownloadRule = false;
          var _ruleGroupOptions = response.data.message;
          _ruleGroupOptions.forEach(item => {
            item.rule_uuid = item.rule_group_uuid;
          })
          t.ruleGroupOptions = t.getDifference(t.tableData, _ruleGroupOptions)
        },
        function () {
          t.loadingDownloadRule = false;
        },
        "no-message"
      );
    },

    getDifference(arr1, arr2) {
      var newArr = [];
      arr2.forEach(item => {
        var flag = true;
        arr1.forEach(i => {
          if (i.rule_uuid == item.rule_uuid) {
            flag = false
          }
        })
        if (flag) {
          newArr.push(item)
        }
      })
      return newArr;
    },
    onClickDownloadRuleSubmit(downloadRuleForm) { 
      var t = this;
      var postUrl = "/waf/waf_load_web_white_rule";
      var oData = {};
      if (t.ruleType == "single_rule") {
        oData = { rule_uuid: t.downloadRuleForm.rule_uuid, domain: t.domain, rule_type: "single_rule" };
        if (t.tabIndex == "group_rule") {
          oData = { rule_uuid: t.downloadRuleForm.rule_uuid, domain: t.domain, rule_type: "group_rule" };
        }
      } else {
        postUrl = "/waf/waf_load_group_web_white_rule";
        oData = { rule_uuid: t.downloadRuleForm.rule_uuid, group_id: t.domain, rule_type: "single_rule" };
        if (t.tabIndex == "group_rule") {
          oData = { rule_uuid: t.downloadRuleForm.rule_uuid, group_id: t.domain, rule_type: "group_rule" };
        }
      }
      this.$refs[downloadRuleForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            postUrl,
            oData,
            function (response) {
              t.loading = false;
              t.dialogDownloadRuleFormVisible = false;
              t.getData();
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },
    dialogCloseDownloadRule() {
      this.downloadRuleForm = [];
      this.$refs["downloadRuleForm"].resetFields();
    },
    formatMatchs(rule_matchs) { 
      var _rule_matchs = JSON.parse(rule_matchs);
          var _ruleBigMatchs = [];
          for (var i in _rule_matchs) {
            var _match = [];
            var _prepocess = [];
            var _default = [
              "cookie",

            ]

            for (var j in _rule_matchs[i].match_args) {
              var item = _rule_matchs[i].match_args[j]
              var key = Object.keys(item)[0]
              var show = "false"
              if (_default.indexOf(key) > -1) {
                show = "true"
              }

              _match.push({
                "rule_match_key_list": [key, item[key]],
                "rule_match_key": key + ':' + item[key],
                "showInput": show
              })
            }
            for (var m in _rule_matchs[i].args_prepocess) {
              _prepocess.push(
                {
                  "args_prepocess_value": _rule_matchs[i].args_prepocess[m]
                }
              )
            }

            _ruleBigMatchs.push({
              ruleSmallMatchs: _match,
              argsPrepocessList: _prepocess,
              match_operator: _rule_matchs[i].match_operator,
              match_value: _rule_matchs[i].match_value
            })
          }
      return _ruleBigMatchs;
    },
    handleLook(data) { 
      var t = this;
      t.ruleBigMatchs = [];
      t.loadingPage = true;
      var postUrl = "/waf/waf_get_sys_web_white_rule";
      var oData = { rule_uuid: data.rule_uuid, rule_type: data.rule_type };
      if (data.rule_type == "group_rule") { 
        postUrl = "/waf/waf_get_sys_web_white_rule_group_list";
        oData = { rule_group_uuid: data.rule_uuid, rule_type: data.rule_type }
      }
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.loadingPage = false;
          if (data.rule_type == "single_rule") { 
            t.lookRuleForm = response.data.message;
            t.ruleBigMatchs = t.formatMatchs(t.lookRuleForm.rule_matchs);
          t.dialogLookRuleFormVisible = true;
          }
          if (data.rule_type == "group_rule") { 
            t.lookRuleGroupData = response.data.message;
            t.lookRuleGroupData.forEach(item => {
              item.ruleBigMatchs = t.formatMatchs(item.rule_matchs);  
            })
            t.dialogLookRuleGroupFormVisible = true;
          }
          
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
      
    },
    handleLookGroup(row) { 
      var t = this;
      t.rowExpend = [];
      t.rowExpend.push(row.rule_uuid);
    },

    handleDelete(data) {
      var t = this;
      t.loading = true;
      var postUrl = "/waf/waf_del_web_white_rule";
      var oData = {
        domain: t.domain,
        rule_uuid: data.rule_uuid
      };
      if (t.ruleType == "group_rule") {
        postUrl = "/waf/waf_del_group_web_white_rule";
        oData = {
          group_id: t.domain,
          rule_uuid: data.rule_uuid
        };
      }
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          data.isVisiblePopover = false;
          t.loading = false;
          t.getData();
        },
        function () {
          t.loading = false;
        }
      );
    },
    onClickChangeOrder() { 
      var t = this;
      t.isShowOrder = !t.isShowOrder;
    },
    onClickChangeOrderSubmit(index, value, operate) { 
      var t = this;
      
      var oData = {
        rule_uuid: value.rule_uuid,
      };
      if (index > 0) { 
        if (operate == "top") {
          oData.type = "top";
        }
        if (operate == "up") {
          oData.type = "exchange";
          oData.exchange_rule_uuid = t.tableData[index - 1].rule_uuid;
        }
      }

      if (index < t.tableData.length - 1) { 
        if (operate == "down") {
          oData.type = "exchange";
          oData.exchange_rule_uuid = t.tableData[index + 1].rule_uuid;
        }
      }
      var postUrl = "/waf/waf_exchange_web_white_rule_priority";

      if (t.ruleType == "group_rule") {
        postUrl = "/waf/waf_exchange_group_web_white_rule_priority";
        oData.group_id = t.domain;
      } else { 
        oData.domain = t.domain;
      }

      if (oData.type == "top" || oData.type == "exchange") { 
        t.orderLoading = true;  
        JXAjax(
          "post",
          postUrl,
          oData,
          function (response) {
            t.orderLoading = false;
            t.getData();
          },
          function () {
            t.orderLoading = false;
          },
          "no-message"
        );
      }
      
    },
    onChangeRuleStatus(value) { 
      var t = this;
      var oData = {
        domain: t.domain,
        rule_uuid: value.rule_uuid,
        rule_status: value.rule_status
      }
      var postUrl = "/waf/waf_edit_web_white_rule";
      if (t.ruleType == "group_rule") {
        postUrl = "/waf/waf_edit_group_web_white_rule";
        oData = {
          group_id: t.domain,
          rule_uuid: value.rule_uuid,
          rule_status: value.rule_status
        }
      }
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.getData();
        },
        function () {
        },
        "no-message"
      );
    }
  },
};
</script>
<style>


.custom-wrap {
  /* max-width: 800px;
  min-width: 400px; */
}
.el-button.button-block {
  display: block;
  margin-left: 0px;
  text-align: right;
  width: 100%;
}

.custom-wrap .rule-matchs-content .match-box-title {
  display: inline-block;
  text-align: left;
  font-size: 14px;
  color: #909399;
  padding: 0 5px;
  box-sizing: border-box;
}
.custom-wrap .rule-matchs-content .match-box-title:before {
  content: "*";
  color: #f56c6c;
  margin-right: 4px;
}
.custom-wrap .match-inline-block {
  width: 192px;
}
.custom-wrap .match-inline-block-small {
  width: 150px;
}
.box-card-rule {
  margin-bottom: 22px;
  border: 1px solid #409eff;
}
.box-card-rule .card-item {
  border-bottom: 1px solid #e0e3e9;
  margin-bottom: 20px;
}
</style>
